import React, { useEffect, useState } from "react";
import Herosection from "../components/Herosection";
import Products from "../components/Products";
import { useDispatch, useSelector } from "react-redux";
import { getProduct } from "../redux/ActionCreators/ProductActionCreators";
import { getMaincategory } from "../redux/ActionCreators/MaincategoryActionCreators";
import { getBrand } from "../redux/ActionCreators/BrandActionCreators";
import { useSearchParams } from "react-router-dom";

export default function ShopPage() {
  let ProductStateData = useSelector((state) => state.ProductStateData);
  let MaincategoryStateData = useSelector(
    (state) => state.MaincategoryStateData
  );
  let BrandStateData = useSelector((state) => state.BrandStateData);

  let [data, setData] = useState([]);
  let [mc, setMc] = useState("");
  let [br, setBr] = useState("");
  let [flag, setFlag] = useState(false);
  let [search, setSearch] = useState("");
  let [min, setMin] = useState(0);
  let [max, setMax] = useState(10000);

  let dispatch = useDispatch();
  let [searchParams, setSearchParams] = useSearchParams();

  useEffect(() => {
    (() => {
      dispatch(getMaincategory());
    })();
  }, [MaincategoryStateData.length]);

  useEffect(() => {
    (() => {
      dispatch(getBrand());
    })();
  }, [BrandStateData.length]);

  function postSearch(e) {
    e.preventDefault();
    console.log(search);
    let ch = search.toLocaleLowerCase();
    setData(
      ProductStateData.filter(
        (x) =>
          (x.active &&
            x.maincategory?.name?.toLocaleLowerCase().includes(ch)) ||
          x.brand?.name?.toLocaleLowerCase().includes(ch) ||
          x.color?.toLocaleLowerCase() === ch ||
          x.description?.toLocaleLowerCase().includes(ch)
      )
    );
  }

  function sortFilter(option) {
    if (option === "1")
      setData(data.sort((x, y) => y._id.localeCompare(x._id)));
    else if (option === "2")
      setData(data.sort((x, y) => y.finalPrice - x.finalPrice));
    else setData(data.sort((x, y) => x.finalPrice - y.finalPrice));

    setFlag(!flag);
  }

  function filterData(mc, br, min = -1, max = -1) {
    setSearch("");
    setData(
      ProductStateData.filter((p) => {
        return (
          (mc === "All" || mc === p.maincategory?.name) &&
          (br === "All" || br === p.brand?.name) &&
          (min === -1 || p.finalPrice >= min) &&
          (max === -1 || p.finalPrice <= max)
        );
      })
    );
  }

  function applyPriceFilter(e) {
    e.preventDefault();
    filterData(mc, br, min, max);
  }

  useEffect(() => {
    (() => {
      dispatch(getProduct());
      let mc = searchParams.get("mc") ?? "All";
      let br = searchParams.get("br") ?? "All";
      if (ProductStateData.length) {
        setMc(mc);
        setBr(br);
        filterData(mc, br);
      }
    })();
  }, [ProductStateData.length, searchParams]);

  return (
    <>
      <Herosection title="Shop Now" />
      <div className="container-fluid my-3 mb-5">
        <div className="row">
          <div className="col-md-2">
            <h5>FILTERS</h5>
            <div className="list-group mb-3">
              <a
                href="#"
                className="list-group-item list-group-item-action bg-info text-white"
                aria-current="true"
              >
                CATEGORIES
              </a>
              <ul className="list-group">
                <li className="list-group-item">
                  <input
                    className="form-check-input me-1 border-1 border-info"
                    type="checkbox"
                    value=""
                    id="firstCheckboxStretched"
                    checked={mc === "All"}
                    onChange={() => {
                      const newMc = mc === "All" ? "" : "All";
                      setMc(newMc);
                      setSearchParams({ mc: newMc, br });
                    }}
                  />
                  <label
                    className="form-check-label stretched-link mx-3"
                    for="firstCheckboxStretched"
                  >
                    All
                  </label>
                </li>
                {MaincategoryStateData.filter((x) => x.active).map((item) => {
                  const isChecked = mc === item.name;
                  const newMc = isChecked ? "All" : item.name;
                  return (
                    <li className="list-group-item" key={item._id}>
                      <input
                        className="form-check-input me-1 border-1 border-info"
                        type="checkbox"
                        value=""
                        id={item._id}
                        checked={isChecked}
                        onChange={() => {
                          setMc(newMc);
                          setSearchParams({ mc: newMc, br });
                        }}
                      />
                      <label
                        className="form-check-label stretched-link mx-3"
                        for={item._id}
                      >
                        {item.name}
                      </label>
                    </li>
                  );
                })}
              </ul>
            </div>
            <hr />
            <div>
              <h5 className="bg-dark text-light text-center p-2">Sort By</h5>
              <select
                name="sortFilter"
                onChange={(e) => sortFilter(e.target.value)}
                className="form-select border-2 border-info"
              >
                <option value="1">Latest</option>
                <option value="2">Price : High to Low</option>
                <option value="3">Price : Low to High</option>
              </select>
            </div>
            <hr />
            <h5 className="bg-dark text-light text-center p-2">Price Filter</h5>
            <form onSubmit={applyPriceFilter}>
              <div className="row">
                <div className="col-6 mb-3">
                  <label>Min</label>
                  <input
                    type="number"
                    name="min"
                    value={min}
                    placeholder="Min"
                    onChange={(e) => setMin(e.target.value)}
                    className="form-control border-2 border-info"
                  />
                </div>
                <div className="col-6 mb-3">
                  <label>Max</label>
                  <input
                    type="number"
                    name="max"
                    value={max}
                    placeholder="Max"
                    onChange={(e) => setMax(e.target.value)}
                    className="form-control border-2 border-info"
                  />
                </div>
              </div>
              <div className="mb-3">
                <button type="submit" className="btn btn-info w-100">
                  Apply Filter
                </button>
              </div>
            </form>
          </div>
          <div className="col-md-10">
            <div className="row">
              <div className="col-md-12 mb-3">
                <form onSubmit={postSearch}>
                  <div className="btn-group w-100">
                    <input
                      type="search"
                      name="search"
                      onChange={(e) => setSearch(e.target.value)}
                      value={search}
                      placeholder="Search Products by Name, Maincategory, Subcategory, Brand , Color Etc..."
                      className="form-control border-2 border-info"
                      style={{ borderRadius: "10px 0 0 10px" }}
                    />
                    <button type="submit" className="btn btn-info w-25">
                      Search
                    </button>
                  </div>
                </form>
              </div>
            </div>
            <Products data={data} />
          </div>
        </div>
      </div>
    </>
  );
}
