import React, { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import "aos/dist/aos.css";
import Aos from "aos";
import { useDispatch, useSelector } from "react-redux";
import {
  createCart,
  getCart,
} from "../redux/ActionCreators/CartActionCreators";
import {
  createWishlist,
  getWishlist,
} from "../redux/ActionCreators/WishlistActionCreators";

export default function Products({ data }) {
  let [qty, setQty] = useState(1);
  let CartStateData = useSelector((state) => state.CartStateData);
  let WishlistStateData = useSelector((state) => state.WishlistStateData);
  let dispatch = useDispatch();
  let navigate = useNavigate();

  const isWishlisted = (productId) => {
    if (!localStorage.getItem("userid")) {
      return false;
    }
    return WishlistStateData.some(
      (x) =>
        x.product?._id === productId &&
        x.user?._id === localStorage.getItem("userid")
    );
  };

  const addToWishlist = (item) => {
    if (!isWishlisted(item._id)) {
      dispatch(
        createWishlist({
          user: localStorage.getItem("userid"),
          product: item._id,
        })
      );
    }
  };

  const isInCart = (productId) => {
    CartStateData.some(
      (x) =>
        x.product?._id === productId &&
        x.user?._id === localStorage.getItem("userid")
    );
  };

  const addToCart = (item) => {
    if (!isInCart(item._id)) {
      dispatch(
        createCart({
          user: localStorage.getItem("userid"),
          product: item._id,
          qty: qty,
          total: item.finalPrice * qty,
        })
      );
      navigate("/cart");
    }
  };

  useEffect(() => {
    (() => {
      let userid = localStorage.getItem("userid");
      if (userid) {
        dispatch(getCart(userid));
      }
    })();
  }, [CartStateData.length]);

  useEffect(() => {
    (() => {
      let userid = localStorage.getItem("userid");
      if (userid) dispatch(getWishlist(userid));
    })();
  }, [WishlistStateData.length]);

  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  return (
    <>
      {/* <!-- Project Start --> */}
      <div className="container-fluid project py-5 mb-5">
        <div className="container">
          <div
            className="text-center mx-auto pb-5"
            data-aos="zoom-in"
            style={{ maxWidth: "600px" }}
          >
            <h3 className="text-info">Featured Products</h3>
            <h1>Summer Collection New Morden Design</h1>
          </div>
          <div className="row g-5">
            {data.map((item) => {
              return (
                <div
                  key={item._id}
                  className="col-md-6 col-lg-4 wow fadeIn"
                  data-wow-delay=".1s"
                >
                  <div className="card-effect">
                    <div className="card" data-aos="zoom-in" style={{}}>
                      <div style={{ position: "relative" }}>
                        {/* Wishlist Icon */}
                        <div
                          className="position-absolute top-0 end-0 m-2 p-2 "
                          style={{ zIndex: 10 }}
                        >
                          <i
                            className="bi bi-heart-fill"
                            style={{
                              color: isWishlisted(item._id) ? "red" : "white",
                              fontSize: "25px",
                              WebkitTextStroke: isWishlisted(item._id)
                                ? "white"
                                : "0.5px black",
                              cursor: "pointer",
                              transition:
                                "transform 0.2s ease, color 0.2s ease",
                              transform: isWishlisted
                                ? "scale(1.3)"
                                : "scale(1)",
                            }}
                            onClick={() => addToWishlist(item)}
                          ></i>
                        </div>
                        <Link to={`/product/${item._id}`}>
                          <img
                            src={`${process.env.REACT_APP_BACKEND_SERVER}/${item.pic[0]}`}
                            className="card-img-top"
                            alt=""
                            style={{ height: "350px" }}
                          />
                        </Link>
                        {/* Rating Badge */}
                        <div
                          className="position-absolute bottom-0 m-2 px-2 py-1 bg-white border border-dark rounded d-flex align-items-center gap-1"
                          style={{ fontSize: "14px", zIndex: 10 }}
                        >
                          <span className="fw-bold">4.5</span>
                          <i className="bi bi-star-fill text-warning"></i>
                          <span className="text-muted">| 2k</span>
                        </div>
                      </div>
                      {/* Card Body */}
                      <div className="card-body">
                        <div className="d-flex align-items-center justify-content-between">
                          <div>
                            <h5 className="card-title">{item.brand.name}</h5>
                            <p className="card-text">{item.name}</p>
                          </div>
                          <div
                            className="bg-info rounded-circle d-flex align-items-center justify-content-center"
                            style={{ width: "30px", height: "30px" }}
                            onClick={() => addToCart(item)}
                          >
                            <i
                              className="bi bi-cart-plus-fill "
                              style={{
                                fontSize: "18px",
                                cursor: "pointer",
                                color: "white",
                              }}
                            ></i>
                          </div>
                        </div>
                        <p>
                          <span className="card-text fw-bold ">
                            Rs. {item.finalPrice}
                          </span>
                          <del className="card-text"> Rs. {item.basePrice}</del>
                          <span className="text-danger">
                            {" "}
                            ({item.discount}% off)
                          </span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      {/* <!-- Project End --> */}
    </>
  );
}
