import React from "react";
import { Link, NavLink, useNavigate } from "react-router-dom";

export default function Navbar() {
  let navigate = useNavigate();
  function logout() {
    localStorage.clear();
    navigate("/login");
  }

  return (
    <>
      <nav className="navbar navbar-expand-lg bg-light shadow-sm py-3 sticky-top">
        <div className="container">
          {/* Logo */}
          <Link
            className="navbar-brand fw-bold text-danger"
            to="/"
            style={{
              fontFamily: "'Poppins', sans-serif",
              letterSpacing: "1px",
              fontSize: "1.5rem",
            }}
          >
            MTaj Dresses
          </Link>

          {/* Hamburger Menu for Mobile */}
          <button
            className="navbar-toggler "
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          {/* Navbar Links */}
          <div className="collapse navbar-collapse" id="navbarNav">
            {/* Center Links */}
            <ul className="navbar-nav mx-auto mb-2 mb-lg-0">
              <li className="nav-item mx-3">
                <NavLink className="nav-link fw-bold" to="/">
                  HOME
                </NavLink>
              </li>
              <li className="nav-item mx-3">
                <NavLink className="nav-link fw-bold" to="/about">
                  ABOUT
                </NavLink>
              </li>
              <li className="nav-item mx-3">
                <NavLink className="nav-link fw-bold" to="/shop">
                  SHOP
                </NavLink>
              </li>
              <li className="nav-item mx-3">
                <NavLink className="nav-link fw-bold" to="/features">
                  FEATURES
                </NavLink>
              </li>
              <li className="nav-item mx-3">
                <NavLink className="nav-link fw-bold" to="/contactus">
                  CONTACT
                </NavLink>
              </li>
              {/* <li className="nav-item mx-3">
                <NavLink className="nav-link fw-bold" to="/admin">
                  Admin
                </NavLink>
              </li> */}
            </ul>
            {/* Right Side Options */}
            <ul className="navbar-nav align-items-center">
              <li className="nav-item dropdown me-2">
                <NavLink
                  className="nav-link dropdown-toggle fw-bold"
                  href="#"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Profile
                </NavLink>
                <ul className="dropdown-menu">
                  <li>
                    {localStorage.getItem("login") ? (
                      <>
                        {localStorage.getItem("role") === "Buyer" ? (
                          <Link
                            className="text-decoration-none mb-0"
                            to="/profile"
                          >
                            <div className="dropdown-header mb-0">
                              <h5 className="mb-0 text-dark">
                                Hello, {localStorage.getItem("name")}
                              </h5>
                              <p>{localStorage.getItem("phone")}</p>
                            </div>
                          </Link>
                        ) : (
                          <Link
                            className="text-decoration-none mb-0"
                            to="/profile"
                          >
                            <div className="dropdown-header mb-0">
                              <h5 className="mb-0 text-dark">
                                Hello, {localStorage.getItem("name")}
                              </h5>
                              <p>{localStorage.getItem("phone")}</p>
                            </div>
                          </Link>
                        )}
                      </>
                    ) : (
                      <>
                        <div className="dropdown-header">
                          <h5>Welcome</h5>
                          <p>To access account and manage orders</p>
                        </div>
                        <Link className="dropdown-item" to="/login">
                          <button className="btn btn-outline-danger">
                            LOGIN / SIGNUP
                          </button>
                        </Link>
                      </>
                    )}
                  </li>
                  {localStorage.getItem("login") ? (
                    <>
                      <hr style={{ margin: "0px" }} />
                      <li>
                        <Link className="dropdown-item" to="/orders">
                          Orders
                        </Link>
                      </li>
                      <li>
                        <button className="dropdown-item" onClick={logout}>
                          Logout
                        </button>
                      </li>
                    </>
                  ) : null}
                </ul>
              </li>
              <li className="nav-item me-2">
                <Link className="nav-link fw-bold " to="/wishlist">
                  Wishlist
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link fw-bold" to="/cart">
                  Bag
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
