import React from "react";
import { Link } from "react-router-dom";

export default function About(title) {
  return (
    <>
      {/* <!-- About Start --> */}
      <div className="container-fluid py-5 my-5">
        <div className="container pt-5">
          <div className="row g-5">
            <div
              className="col-lg-5 col-md-6 col-sm-12 wow fadeIn"
              data-wow-delay=".3s"
            >
              <div className="h-100 position-relative">
                <img
                  src="img/about-1.jpg"
                  className="img-fluid w-75 rounded"
                  alt=""
                  style={{ marginBottom: "25%" }}
                />
                <div
                  className="position-absolute w-75"
                  style={{ top: "25%", left: "25%" }}
                >
                  <img
                    src="img/banner6.jpg"
                    className="img-fluid w-100 rounded"
                    alt=""
                  />
                </div>
              </div>
            </div>
            <div
              className="col-lg-7 col-md-6 col-sm-12 wow fadeIn"
              data-wow-delay=".5s"
            >
              <h1 className="text-info">About Us</h1>
              <h2 className="mb-4">About MTaj Dresses</h2>
              <p>
                At MTaj Dresses, we believe in celebrating childhood with style,
                elegance, and tradition. Specializing in designer wear for
                girls, we craft a wide range of ethnic and festive outfits
                including shararas, lehengas, Mastani dresses, frocks, and more.
                Each piece is thoughtfully designed to combine comfort with
                cultural charm, making every little girl feel like royalty. With
                a strong background in garment manufacturing, MTaj Dresses
                blends fine craftsmanship with high-quality fabrics and trendy
                designs.
              </p>
              <p className="mb-4">
                Whether it's a family celebration, festival, or wedding, our
                outfits ensure your child looks her best while feeling confident
                and comfortable. Rooted in tradition yet tailored for the modern
                world, MTaj Dresses is your trusted destination for stylish and
                affordable ethnic wear for girls.
              </p>
              {title && (
                <Link
                  to="/about"
                  className="btn btn-secondary rounded-pill w-100 py-2 text-white"
                >
                  More Details
                </Link>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* <!-- About End --> */}
    </>
  );
}
