import React, { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-coverflow";
import "swiper/css/effect-cards";
import "swiper/css/pagination";

import { EffectCards, EffectCoverflow, Pagination } from "swiper/modules";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { createCart, getCart } from "../redux/ActionCreators/CartActionCreators";

export default function ProductSlider({ title, data }) {
  let [qty, setQty] = useState(1);
  let CartStateData = useSelector((state) => state.CartStateData);
  let dispatch = useDispatch();
  let navigate = useNavigate();

  let [slidesPerView, setSlidesPerView] = useState(
    window.innerWidth < 1000 ? 1 : 3
  );
  let [slideType, setSlideType] = useState(
    window.innerWidth < 1000 ? "cards" : "coverflow"
  );
  let options = {
    effect: slideType,
    grabCursor: true,
    centeredSlides: false,
    slidesPerView: slidesPerView,
    coverflowEffect: {
      rotate: 50,
      stretch: 0,
      depth: 100,
      modifier: 1,
      slideShadows: true,
    },
    loop: true,
    pagination: { clickable: true },
    modules: [EffectCoverflow, Pagination, EffectCards],
    className: "mySwiper",
  };
  function handelWindoResize() {
    setSlidesPerView(window.innerWidth < 1000 ? 1 : 3);
    setSlideType(window.innerWidth < 1000 ? "cards" : "coverflow");
    navigate(0);
  }
  window.addEventListener("resize", handelWindoResize);

  const isInCart = (productId) => {
    CartStateData.some(
      (x) =>
        x.product?._id === productId &&
        x.user?._id === localStorage.getItem("userid")
    );
  };

  const addToCart = (item) => {
    if (!isInCart(item._id)) {
      dispatch(
        createCart({
          user: localStorage.getItem("userid"),
          product: item._id,
          qty: qty,
          total: item.finalPrice * qty,
        })
      );
      navigate("/cart");
    }
  };

  useEffect(() => {
    (() => {
      dispatch(getCart());
    })();
  }, [CartStateData.length]);
  return (
    <>
      {/* <!-- Testimonial Start --> */}
      <div className="container-fluid testimonial py-5 mb-5">
        <div className="container">
          <div
            className="text-center mx-auto pb-5 wow fadeIn"
            data-wow-delay=".3s"
            style={{ maxWidth: "500px" }}
          >
            <h5 className="text-primary">
              {title ? title : "Our Latest Products"}
            </h5>
            <h1>
              Checkout Our {title ? title : "Latest Products"} of Top Quality
            </h1>
          </div>
          <Swiper {...options}>
            {data?.map((item) => {
              return (
                <SwiperSlide key={item._id}>
                  <div className="card-effect">
                    <div className="card">
                      <Link to={`/product/${item._id}`}>
                        <img
                          src={`${process.env.REACT_APP_BACKEND_SERVER}/${item.pic[0]}`}
                          style={{ height: 350 }}
                          className="card-img-top"
                          alt=""
                        />
                      </Link>
                      <div className="card-title text-center py-3">
                        <h4 className="">{item.name}</h4>
                        <p className="m-0">
                          <span className="card-text fw-bold ">
                            Rs. {item.finalPrice}
                          </span>
                          <del className="card-text"> Rs. {item.basePrice}</del>
                          <span className="text-danger">
                            {" "}
                            ({item.discount}% off)
                          </span>
                        </p>
                      </div>
                      <div className="team-icon d-flex justify-content-center pb-4">
                        <button
                          className="btn btn-secondary text-white m-1 mb-4"
                          onClick={() => addToCart(item)}
                        >
                          <i className="fa fa-shopping-cart"></i> Add to Cart
                        </button>
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </div>

      {/* <!-- Testimonial End --> */}
    </>
  );
}
