import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";

export default function ForgetPassword1() {
  let [username, setUsername] = useState("");
  let [errorMessage, setErrorMessage] = useState("");

  let navigate = useNavigate();

  function getInputData(e) {
    setUsername(e.target.value);
  }

  async function postData(e) {
    e.preventDefault();
    try {
      let response = await fetch(
        `${process.env.REACT_APP_BACKEND_SERVER}/api/user/forget-password-1`,
        {
          method: "POST",
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify({ username: username }),
        }
      );
      response = await response.json();

      if (response.result === "Done") {
        localStorage.setItem("reset-password-username", username);
        navigate("/forget-password-2");
      } else setErrorMessage("User Not Found");
    } catch (error) {
      alert("Internal Server Error");
    }
  }
  return (
    <>
      <div className="container-fluid min-vh-100 d-flex align-items-center justify-content-center bg-light">
        <div
          className="row w-100 shadow-lg rounded bg-white overflow-hidden"
          style={{ maxWidth: "900px" }}
        >
          {/* Left Side */}
          <div className="col-lg-6 p-0 d-none d-lg-block">
            <div
              style={{
                height: "100%",
                backgroundImage: "url(/img/banner4.jpg)",
                backgroundSize: "cover",
                backgroundPosition: "center",
              }}
            >
              <div
                className="h-100 d-flex flex-column align-items-center justify-content-center text-white p-4 fw-bold"
                style={{ background: "rgba(0,0,0,0.3)" }}
              >
                <h4>Welcome to M TAJ DRESSES WebSite</h4>
                <small>www.mtajdresses.in</small>
              </div>
            </div>
          </div>

          {/* Right Side */}
          <div className="col-lg-6 p-5">
            <h3 className="mb-4 text-center text-danger">MTAJDRESSES.in</h3>

            {/* <div className="d-grid gap-2 mb-3">
              <button className="btn btn-outline-primary fw-bold position-relative">
                <i
                  className="fab fa-facebook-f position-absolute start-0 top-50 translate-middle-y ms-2"
                  style={{ color: "navy" }}
                ></i>
                Sign in with Facebook
              </button>
              <button className="btn btn-outline-danger fw-bold">
                Sign in with Google
              </button>
            </div> */}

            <div className="text-center my-3 fw-bold">Reset your Password</div>

            <form onSubmit={postData}>
              <div className="mb-3">
                <input
                  type="text"
                  name="username"
                  placeholder="Registered Email Address or Username"
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    errorMessage ? "border-danger" : "border-info"
                  }`}
                />
                {errorMessage ? (
                  <p className="text-danger">{errorMessage}</p>
                ) : null}
              </div>

              <button type="submit" className="btn btn-danger w-100">
                Send OTP
              </button>
            </form>

            <div className="text-center mt-3">
              Don't have an account?{" "}
              <Link to="/signup" className="text-danger text-decoration-none">
                Free sign up
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
