import React, { useState } from "react";
import { Link } from "react-router-dom";

export default function Footer() {
  let [email, setEmail] = useState("");
  let [message, setMessage] = useState("");

  async function postData(e) {
    e.preventDefault();
    if (email.length === 0) setMessage("Please Enter a Valid Email Address");
    else {
      let response = await fetch(
        `${process.env.REACT_APP_BACKEND_SERVER}/api/newsletter`,
        {
          method: "POST",
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify({ email: email }),
        }
      );
      response = await response.json();
      console.log(response);
      if (response.result === "Done") {
        setMessage("Thanks to Subscribe Our Newsletter Service");
        setEmail("");
      } else {
        setMessage(response.reason?.email);
      }
    }
  }

  return (
    <>
      {/* <!-- Footer Start --> */}
      <div
        className="container-fluid footer background wow fadeIn pt-5 pb-4"
        data-wow-delay=".3s"
      >
        <div className="row g-5">
          <div className="col-lg-3 col-md-6">
            <Link to="/" className="text-decoration-none">
              <h1 className="text-light fw-bold d-block">
                MTaj<span className="text-info">Dresses</span>{" "}
              </h1>
            </Link>
            <p className="mt-4 text-light text-justify">
              We offer up to 90% off on premium ethnic wear for girls. We specializes in beautifully crafted shararas, lehengas, frocks, Mastani dresses, and more—perfect for every celebration.
            </p>
            <div className="d-flex hightech-link">
              <Link
                to="#"
                target="_blank"
                rel="noreferrer"
                className="btn-light nav-fill btn btn-square rounded-circle me-2"
              >
                <i className="fab fa-facebook-f text-primary"></i>
              </Link>
              <Link
                to="#"
                target="_blank"
                rel="noreferrer"
                className="btn-light nav-fill btn btn-square rounded-circle me-2"
              >
                <i className="fab fa-twitter text-primary"></i>
              </Link>
              <Link
                to="#"
                target="_blank"
                rel="noreferrer"
                className="btn-light nav-fill btn btn-square rounded-circle me-2"
              >
                <i className="fab fa-instagram text-primary"></i>
              </Link>
              <Link
                to="#"
                target="_blank"
                rel="noreferrer"
                className="btn-light nav-fill btn btn-square rounded-circle me-0"
              >
                <i className="fab fa-linkedin-in text-primary"></i>
              </Link>
            </div>
          </div>
          <div className="col-lg-3 col-md-6">
            <h3 className="h3 text-light">Contact Us</h3>
            <div className="text-white mt-4 d-flex flex-column contact-link">
              <Link
                to="/"
                className="pb-3 text-light border-bottom border-info text-decoration-none"
              >
                <i className="fas fa-map-marker-alt text-light me-2 "></i> I-48
                Sana Store, Okhla, Delhi, India
              </Link>
              <Link
                to="tel:+919113440016"
                target="_blank"
                rel="noreferrer"
                className="py-3 text-light border-bottom border-info text-decoration-none"
              >
                <i className="fas fa-phone-alt text-light me-2"></i> +91 911 344
                0016
              </Link>
              <Link
                to="http://wa.me/+919113440016"
                target="_blank"
                rel="noreferrer"
                className="py-3 text-light border-bottom border-info text-decoration-none"
              >
                <i className="bi bi-whatsapp fs-5 text-light me-2"></i> +91 911
                344 0016
              </Link>
              <Link
                to="mailto:ansariwasim0663@gmail.com"
                target="_blank"
                rel="noreferrer"
                className="py-3 text-light border-bottom border-info text-decoration-none"
              >
                <i className="fas fa-envelope text-light me-2"></i>{" "}
                ansariwasim0663@gmail.com
              </Link>
            </div>
          </div>
          <div className="col-lg-3 col-md-6">
            <h3 href="#" className="h3 text-light">
              Quick Link
            </h3>
            <div className="mt-4 d-flex flex-column short-link">
              <Link to="/" className="mb-2 text-white">
                <i className="fas fa-angle-right text-secondary me-2"></i>Home
              </Link>
              <Link to="/about" className="mb-2 text-white">
                <i className="fas fa-angle-right text-secondary me-2"></i>About
              </Link>
              <Link to="/shop" className="mb-2 text-white">
                <i className="fas fa-angle-right text-secondary me-2"></i>Shop
              </Link>
              <Link to="/features" className="mb-2 text-white">
                <i className="fas fa-angle-right text-secondary me-2"></i>
                Features
              </Link>
              {/* <Link to="/testimonial" className="mb-2 text-white">
                <i className="fas fa-angle-right text-secondary me-2"></i>
                Testimonials
              </Link> */}
              <Link to="/contactus" className="mb-2 text-white">
                <i className="fas fa-angle-right text-secondary me-2"></i>
                Contact Us
              </Link>
            </div>
          </div>
          <div className="col-lg-3 col-md-12">
            <h3 className="h3 text-light mb-4">Newsletter</h3>
            <h6 className="text-light text-justify">
              {message
                ? message
                : "Subscribe Our Newsletter Service to Get Latest Update About Our New Products and Great Deals"}
            </h6>
            <form onSubmit={postData}>
              <div className="mb-3">
                <input
                  type="email"
                  name="email"
                  onChange={(e) => setEmail(e.target.value)}
                  value={email}
                  placeholder="Email Address"
                  className="form-control"
                />
              </div>
              <button className="btn btn-secondary w-100" type="submit">
                Suscribe
              </button>
            </form>
          </div>
        </div>
        <hr className="text-light mt-5 mb-4" />
        <div className="row">
          <div className="col-md-6 text-center text-md-start">
            <span className="text-light">
              <Link to="#" className="text-info text-decoration-none">
                <i className="fas fa-copyright text-white me-2"></i>MTajDresses
              </Link>
              , All right reserved.
            </span>
          </div>
        </div>
      </div>
      {/* <!-- Footer End --> */}
    </>
  );
}
