import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import {
  deleteWishlist,
  getWishlist,
} from "../redux/ActionCreators/WishlistActionCreators";
import "aos/dist/aos.css";
import Aos from "aos";
import {
  createCart,
  getCart,
} from "../redux/ActionCreators/CartActionCreators";

export default function Wishlist({ data }) {
  let [qty, setQty] = useState(1);
  let [wishlist, setWishlist] = useState([]);
  let WishlistStateData = useSelector((state) => state.WishlistStateData);
  let CartStateData = useSelector((state) => state.CartStateData);
  let dispatch = useDispatch();
  let navigate = useNavigate();

  function deleteRecord(_id) {
    dispatch(deleteWishlist({ _id: _id }));
    getAPIData();
  }

  function getAPIData() {
    dispatch(getWishlist());
    if (WishlistStateData.length) setWishlist(WishlistStateData);
    else setWishlist([]);
  }

  function addToCart(product) {
    let item = CartStateData.find(
      (x) =>
        x.product?._id === product._id &&
        x.user?._id === localStorage.getItem("userid")
    );
    if (!item) {
      dispatch(
        createCart({
          user: localStorage.getItem("userid"),
          product: product._id,
          qty: qty,
          total: product.finalPrice * qty,
        })
      );
    }
    navigate("/cart");
  }

  useEffect(() => {
    (() => {
      dispatch(getCart());
    })();
  }, [CartStateData.length]);

  useEffect(() => {
    getAPIData();
  }, [WishlistStateData.length]);

  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  return (
    <>
      {/* <!-- Project Start --> */}
      <div className="container-fluid project py-5 mb-5">
        <div className="container">
          <div
            className="text-center mx-auto pb-5"
            style={{ maxWidth: "600px" }}
          >
            <h3 className="text-info">My Wishlist <span className="text-secondary">({wishlist.length} items)</span></h3>
          </div>
          <div className="row g-5">
            {wishlist.map((item) => {
              return (
                <div
                  key={item._id}
                  className="col-md-6 col-lg-4 wow fadeIn"
                  data-wow-delay=".1s"
                >
                  <div className="card-effect">
                    <div className="card" data-aos="zoom-in" style={{}}>
                      <div className="position-relative">
                        {/* Wishlist Icon */}
                        <div
                          className="position-absolute top-0 end-0 m-2 p-2 "
                          style={{ zIndex: 10 }}
                        >
                          <i
                            className="bi bi-x-circle-fill"
                            style={{
                              color: "gray",
                              fontSize: "25px",
                              WebkitTextStroke: ".5px white",
                              cursor: "pointer",
                              transition:
                                "transform 0.2s ease, color 0.2s ease",
                              transform: "scale(1.5)",
                            }}
                            onClick={() => deleteRecord(item._id)}
                          ></i>
                        </div>
                        <Link
                          to={`${process.env.REACT_APP_BACKEND_SERVER}/${item.product?.pic[0]}`}
                          target="_blank"
                          rel="noreferrer"
                        >
                          <img
                            src={`${process.env.REACT_APP_BACKEND_SERVER}/${item.product?.pic[0]}`}
                            className="card-img-top"
                            alt=""
                            style={{ height: "350px" }}
                          />
                        </Link>
                      </div>
                      {/* Card Body */}
                      <div className="card-body">
                        <div className="d-flex align-items-center justify-content-between">
                          <div>
                            <h5 className="card-title">
                              {item.product?.brand?.name}
                            </h5>
                            <p className="card-text">{item.product?.name}</p>
                          </div>
                          <div
                            className="bg-info rounded-circle d-flex align-items-center justify-content-center"
                            style={{ width: "30px", height: "30px" }}
                            onClick={() => addToCart(item.product)}
                          >
                            <i
                              className="bi bi-cart-plus-fill "
                              style={{
                                fontSize: "18px",
                                cursor: "pointer",
                                color: "white",
                              }}
                            ></i>
                          </div>
                        </div>
                        <p>
                          <span className="card-text fw-bold ">
                            Rs. {item.product?.finalPrice}
                          </span>
                          <del className="card-text">
                            {" "}
                            Rs. {item.product?.basePrice}
                          </del>
                          <span className="text-danger">
                            {" "}
                            ({item.product?.discount}% off)
                          </span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      {/* <!-- Project End --> */}
    </>
  );
}
