import React, { useEffect, useState } from "react";
import AdminSidebar from "../../../components/AdminSidebar";
import Herosection from "../../../components/Herosection";
import { Link, useNavigate } from "react-router-dom";

import formValidators from "../../../FormValidators/formValidators";
import imageValidators from "../../../FormValidators/imageValidators";

import { useDispatch, useSelector } from "react-redux";
import {
  createBrand,
  getBrand,
} from "../../../redux/ActionCreators/BrandActionCreators";

export default function AdminCreateBrand() {
  let BrandStateData = useSelector(
    (state) => state.BrandStateData
  );
  let dispatch = useDispatch();
  let [data, setData] = useState({
    name: "",
    pic: "",
    active: true,
  });
  let [error, setError] = useState({
    name: "Name Field is Mandatory",
    pic: "Pic Field is Mandatory",
  });
  let [show, setShow] = useState(false);
  let navigate = useNavigate();

  function getInputData(e) {
    let name = e.target.name;
    let value = e.target.files ? e.target.files[0] : e.target.value; //in case of real API(backend)
    // let value = e.target.files ? "Brand/" + e.target.files[0].name : e.target.value

    if (name !== "active") {
      setError((old) => {
        return {
          ...old,
          [name]: e.target.files ? imageValidators(e) : formValidators(e),
        };
      });
    }

    setData((old) => {
      return {
        ...old,
        [name]: name === "active" ? (value === "1" ? true : false) : value,
      };
    });
  }

  function postSubmit(e) {
    e.preventDefault();
    let errorItem = Object.values(error).find((x) => x !== "");
    if (errorItem) {
      setShow(true);
    } else {
      let item = BrandStateData.find(
        (x) => x.name.toLocaleLowerCase() === data.name.toLocaleLowerCase()
      );
      if (item) {
        setShow(true);
        setError((old) => {
          return {
            ...old,
            name: "Brand Already Exist",
          };
        });
      } else {
        let formData = new FormData();
        formData.append("name", data.name);
        formData.append("pic", data.pic); // data.pic should be a File object
        formData.append("active", data.active);
        dispatch(createBrand(formData));

        navigate("/admin/brand");
      }
    }
  }

  useEffect(() => {
    (() => {
      dispatch(getBrand());
    })();
  }, [BrandStateData.length]);

  return (
    <>
      <Herosection title={"Admin Page"} />
      <div className="container-fluid mb-5 py-5">
        <div className="row">
          <div className="col-md-3">
            <AdminSidebar />
          </div>
          <div className="col-md-9">
            <h5 className="bg-info text-white text-center p-2">
              Brand{" "}
              <Link
                to="/admin/brand"
                className="fa fa-arrow-left text-light float-end text-decoration-none"
              ></Link>
            </h5>
            <form onSubmit={postSubmit}>
              <div className="mb-3">
                <label>Name*</label>
                <input
                  type="text"
                  name="name"
                  onChange={getInputData}
                  placeholder="Brand name"
                  className={`form-control border-2 ${
                    show && error.name ? "border-danger" : "border-info"
                  }`}
                />
                {show && error.name ? (
                  <p className="text-danger text-capitalize">{error.name}</p>
                ) : null}
              </div>

              <div className="row">
                <div className="col-md-6 mb-3">
                  <label>Pic*</label>
                  <input
                    type="file"
                    name="pic"
                    onChange={getInputData}
                    className={`form-control border-2 ${
                      show && error.pic ? "border-danger" : "border-info"
                    }`}
                  />
                  {show && error.pic ? (
                    <p className="text-danger text-capitalize">{error.pic}</p>
                  ) : null}
                </div>
                <div className="col-md-6 mb-3">
                  <label>Active*</label>
                  <select
                    name="active"
                    onChange={getInputData}
                    className="form-select border-2 border-info"
                  >
                    <option value="1">Yes</option>
                    <option value="0">No</option>
                  </select>
                </div>
              </div>

              <div className="mb-3">
                <button type="submit" className="btn btn-info w-100">
                  Create
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
