import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";

export default function LoginPage() {
  let [data, setData] = useState({
    email: "",
    password: "",
  });

  let [errorMessage, setErrorMessage] = useState("");

  let navigate = useNavigate();

  function getInputData(e) {
    let { name, value } = e.target;
    setData((old) => {
      return {
        ...old,
        [name]: value,
      };
    });
  }

  async function postData(e) {
    e.preventDefault();
    try {
      let response = await fetch(
        `${process.env.REACT_APP_BACKEND_SERVER}/api/user/login`,
        {
          method: "POST",
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify({
            username: data.username,
            password: data.password,
          }),
        }
      );
      response = await response.json();

      if (response.result === "Done" && response.data.active === false) {
        setErrorMessage(
          "Your Account is not Active. Please Contact Us for More Details and To Activate Your Account Please Provide your Username or Registered Email Address in Contact Us Query"
        );
      } else if (response.result === "Done") {
        localStorage.setItem("login", true);
        localStorage.setItem("name", response.data.name);
        localStorage.setItem("userid", response.data._id);
        localStorage.setItem("phone", response.data.phone);
        localStorage.setItem("role", response.data.role);
        localStorage.setItem("token", response.token);
        if (response.data.role === "Buyer") navigate("/");
        else navigate("/admin");
      } else {
        setErrorMessage("Invalid Username or Password");
      }
    } catch (error) {
      alert("Internal Server Error");
    }
  }

  return (
    <>
      <div className="container-fluid min-vh-100 d-flex align-items-center justify-content-center bg-light">
        <div
          className="row w-100 shadow-lg rounded bg-white overflow-hidden"
          style={{ maxWidth: "900px" }}
        >
          {/* Left Side */}
          <div className="col-lg-6 p-0 d-none d-lg-block">
            <div
              style={{
                height: "100%",
                backgroundImage: "url(/img/banner4.jpg)",
                backgroundSize: "cover",
                backgroundPosition: "center",
              }}
            >
              <div
                className="h-100 d-flex flex-column align-items-center justify-content-center text-white p-4 fw-bold"
                style={{ background: "rgba(0,0,0,0.3)" }}
              >
                <h4>Welcome to M TAJ DRESSES WebSite</h4>
                <small>www.mtajdresses.in</small>
              </div>
            </div>
          </div>

          {/* Right Side */}
          <div className="col-lg-6 p-5">
            <h3 className="mb-4 text-center text-danger">MTAJDRESSES.in</h3>

            {/* <div className="d-grid gap-2 mb-3">
              <button className="btn btn-outline-primary fw-bold position-relative">
                <i
                  className="fab fa-facebook-f position-absolute start-0 top-50 translate-middle-y ms-2"
                  style={{ color: "navy" }}
                ></i>
                Sign in with Facebook
              </button>
              <button className="btn btn-outline-danger fw-bold">
                Sign in with Google
              </button>
            </div> */}

            <div className="text-center my-3 fw-bold">
              Log in to your account
            </div>

            <form onSubmit={postData}>
              <div className="mb-3">
                <input
                  type="text"
                  name="username"
                  placeholder="Email or Username"
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    errorMessage ? "border-danger" : "border-info"
                  }`}
                />
                {errorMessage ? (
                  <p className="text-danger">{errorMessage}</p>
                ) : null}
              </div>
              <div className="mb-3">
                <input
                  type="password"
                  name="password"
                  className="form-control"
                  placeholder="Password"
                  onChange={getInputData}
                />
              </div>
              <div className="text-end mt-2 mb-3">
                <Link
                  to="/forget-password-1"
                  className="text-danger text-decoration-none"
                >
                  Forgot password?
                </Link>
              </div>
              <button type="submit" className="btn btn-danger w-100">
                Sign IN
              </button>
            </form>

            <div className="text-center mt-3">
              Don't have an account?{" "}
              <Link to="/signup" className="text-danger text-decoration-none">
                Free sign up
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
