import React, { useEffect, useState } from "react";
import Herosection from "../components/Herosection";
import Profile from "../components/Profile";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getCheckout } from "../redux/ActionCreators/CheckoutCreators";
import Cart from "../components/Cart";

export default function ProfilePage() {
  let [orders, setOrders] = useState([]);

  let CheckoutStateData = useSelector((state) => state.CheckoutStateData);
  let dispatch = useDispatch();

  useEffect(() => {
    (() => {
      dispatch(getCheckout());
      if (CheckoutStateData.length) {
        setOrders(CheckoutStateData);
      }
    })();
  }, [CheckoutStateData.length]);
  return (
    <>
      <Herosection title={"My Profile"} />
      <div className="container-fluid mt-3 mb-5">
        <Profile title={"Buyer"} />
        <h5 className="bg-primary text-light text-center p-2">
          Orders History Section
        </h5>
        {orders.length ? (
          <>
            {orders.map((item) => {
              return (
                <div className="row" key={item._id}>
                  <div className="col-md-4">
                    <div className="table-responsive">
                      <table className="table table-bordered">
                        <tbody>
                          <tr>
                            <th>Order ID</th>
                            <td>{item._id}</td>
                          </tr>
                          <tr>
                            <th>Order Status</th>
                            <td>{item.orderStatus}</td>
                          </tr>
                          <tr>
                            <th>Payment Mode</th>
                            <td>{item.paymentMode}</td>
                          </tr>
                          <tr>
                            <th>Payment Status</th>
                            <td>{item.paymentStatus}</td>
                          </tr>
                          <tr>
                            <th>Subtotal</th>
                            <td>&#8377;{item.subtotal}</td>
                          </tr>
                          <tr>
                            <th>Shipping</th>
                            <td>&#8377;{item.shipping}</td>
                          </tr>
                          <tr>
                            <th>Total</th>
                            <td>&#8377;{item.total}</td>
                          </tr>
                          <tr>
                            <th>Date</th>
                            <td>{new Date(item.createdAt).toDateString()}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div className="col-md-8">
                    <Cart title="Checkout" data={item.products} />
                  </div>
                </div>
              );
            })}
          </>
        ) : (
          <div className="mb-5 text-center mt-3 mb-5">
            <h3>No Order History Found</h3>
            <Link to="/shop" className="btn btn-primary">
              Shop Now
            </Link>
          </div>
        )}
      </div>
      <div className="pb-5"></div>
    </>
  );
}
