import React from "react";
import { Link } from "react-router-dom";

export default function AdminSidebar() {
  return (
    <div className="list-group">
      <Link
        to="/admin"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-home fs-4"></i>
        <span className="float-end text-light">Home</span>
      </Link>
      <Link
        to="/admin/maincategory"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-list fs-4"></i>
        <span className="float-end text-light">Maincategory</span>
      </Link>
      <Link
        to="/admin/brand"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-list fs-4"></i>
        <span className="float-end text-light">Brand</span>
      </Link>
      <Link
        to="/admin/product"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-list fs-4"></i>
        <span className="float-end text-light">Product</span>
      </Link>
      {/* <Link
        to="/admin/testimonial"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-star fs-4"></i>
        <span className="float-end text-light">Testimonial</span>
      </Link> */}
      <Link
        to="/admin/user"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-users fs-4"></i>
        <span className="float-end text-light">User</span>
      </Link>
      <Link
        to="/admin/newsletter"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-envelope fs-4"></i>
        <span className="float-end text-light">Newsletter</span>
      </Link>
      <Link
        to="/admin/checkout"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-shopping-cart fs-4"></i>
        <span className="float-end text-light">Checkout</span>
      </Link>
      <Link
        to="/admin/contactus"
        className="list-group-item list-group-item-action bg-info mb-1"
        aria-current="true"
      >
        <i className="fa fa-phone fs-4"></i>
        <span className="float-end text-light">Contact Us</span>
      </Link>
    </div>
  );
}
