import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import formValidators from "../FormValidators/formValidators";

export default function SignUpPage() {
  let [data, setData] = useState({
    name: "",
    username: "",
    email: "",
    phone: "",
    password: "",
    cpassword: "",
  });
  let [errorMessage, setErrorMessage] = useState({
    name: "Full Name Field Is Mendatory",
    username: "User Name Field Is Mendatory",
    email: "Email Address Field Is Mendatory",
    phone: "Phone Number Field Is Mendatory",
    password: "Password Field Is Mendatory",
  });
  let [show, setShow] = useState(false);
  let navigate = useNavigate();

  function getInputData(e) {
    let { name, value } = e.target;
    setErrorMessage((old) => {
      return {
        ...old,
        [name]: formValidators(e),
      };
    });
    setData((old) => {
      return {
        ...old,
        [name]: value,
      };
    });
  }

  async function postData(e) {
    e.preventDefault();
    if (data.password === data.cpassword) {
      let error = Object.values(errorMessage).find((x) => x !== "");
      if (error) {
        setShow(true);
      } else {
        try {
          let response = await fetch(
            `${process.env.REACT_APP_BACKEND_SERVER}/api/user`,
            {
              method: "POST",
              headers: {
                "content-type": "application/json",
              },
              body: JSON.stringify({
                name: data.name,
                username: data.username,
                email: data.email,
                phone: data.phone,
                password: data.password,
                role: "Buyer",
                active: true,
              }),
            }
          );
          response = await response.json();
          console.log(response);
          if (response.result === "Done") {
            localStorage.setItem("login", true);
            localStorage.setItem("name", response.data.name);
            localStorage.setItem("phone", response.data.phone);
            localStorage.setItem("userid", response.data._id);
            localStorage.setItem("role", response.data.role);
            navigate("/login");
          } else {
            setShow(true);
            setErrorMessage((old) => {
              return {
                ...old,
                username: response.reason?.username ?? "",
                email: response.reason?.email ?? "",
              };
            });
          }
        } catch (error) {
          alert("Internal Server Error");
        }
      }
    } else {
      setShow(true);
      setErrorMessage((old) => {
        return {
          ...old,
          password: "Password and Confirm password does not match",
        };
      });
    }
  }

  return (
    <>
      <div className="container-fluid min-vh-100 d-flex align-items-center justify-content-center bg-light">
        <div
          className="row w-100 shadow-lg rounded bg-white overflow-hidden"
          style={{ maxWidth: "900px" }}
        >
          {/* Left Side */}
          <div className="col-lg-6 p-0 d-none d-lg-block">
            <div
              style={{
                height: "100%",
                backgroundImage: "url(/img/banner4.jpg)",
                backgroundSize: "cover",
                backgroundPosition: "center",
              }}
            >
              <div
                className="h-100 d-flex flex-column align-items-center justify-content-center text-white p-4 fw-bold"
                style={{ background: "rgba(0,0,0,0.3)" }}
              >
                <h4>Welcome to M TAJ DRESSES WebSite</h4>
                <small>www.mtajdresses.in</small>
              </div>
            </div>
          </div>

          {/* Right Side */}
          <div className="col-lg-6 p-5">
            <h3 className="mb-4 text-center text-danger">MTAJDRESSES.in</h3>

            {/* <div className="d-grid gap-2 mb-3">
              <button className="btn btn-outline-primary fw-bold position-relative">
                <i
                  className="fab fa-facebook-f position-absolute start-0 top-50 translate-middle-y ms-2"
                  style={{ color: "navy" }}
                ></i>
                Sign in with Facebook
              </button>
              <button className="btn btn-outline-danger fw-bold">
                Sign in with Google
              </button>
            </div> */}

            <div className="text-center my-3 fw-bold">
              Fill Out the form to sign Up
            </div>

            <form onSubmit={postData}>
              <div className="mb-3">
                <input
                  type="text"
                  name="name"
                  placeholder="Full Name"
                  value={data.name}
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    show && errorMessage.name ? "border-danger" : "border-info"
                  }`}
                />
                {show && errorMessage.name ? (
                  <p className="text-danger">{errorMessage.name}</p>
                ) : null}
              </div>
              <div className="mb-3">
                <input
                  type="text"
                  name="username"
                  placeholder="User Name"
                  value={data.username}
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    show && errorMessage.username
                      ? "border-danger"
                      : "border-info"
                  }`}
                />
                {show && errorMessage.username ? (
                  <p className="text-danger">{errorMessage.username}</p>
                ) : null}
              </div>
              <div className="mb-3">
                <input
                  type="email"
                  name="email"
                  placeholder="Email"
                  value={data.email}
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    show && errorMessage.email ? "border-danger" : "border-info"
                  }`}
                />
                {show && errorMessage.email ? (
                  <p className="text-danger">{errorMessage.email}</p>
                ) : null}
              </div>
              <div className="mb-3">
                <input
                  type="password"
                  name="password"
                  placeholder="Password"
                  value={data.password}
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    show && errorMessage.password
                      ? "border-danger"
                      : "border-info"
                  }`}
                />
                {show && errorMessage.password ? (
                  <p className="text-danger">{errorMessage.password}</p>
                ) : null}
              </div>
              <div className="mb-3">
                <input
                  type="password"
                  name="cpassword"
                  placeholder="Confirm Password"
                  value={data.cpassword}
                  onChange={getInputData}
                  className={`form-control border-2 ${
                    show && errorMessage.password
                      ? "border-danger"
                      : "border-info"
                  }`}
                />
              </div>
              <div className="mb-3">
                <input
                  type="tel"
                  name="phone"
                  className="form-control"
                  placeholder="Phone number"
                  value={data.phone}
                  onChange={getInputData}
                />
              </div>
              <button type="submit" className="btn btn-danger w-100">
                Sign up
              </button>
            </form>

            <div className="text-center mt-3">
              Already have an account?{" "}
              <Link to="/login" className="text-danger text-decoration-none">
                Log in
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
